.user-post-tracker {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #f8f8f8; */
  padding: 1rem;
  /* border-radius: 8px; */
}

.empl-post-search-wrapper {
  width: 70%;
  background-color: #F2F2F2;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 20px;
}

.post-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.post-summary {
  /* border: 1px solid #ddd; */
  cursor: pointer;
  /* transition: box-shadow 0.2s ease; */
  cursor: pointer;
  /* transition: box-shadow 0.2s ease; */
  display: flex;
  align-items: center;
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 10px;
  gap: 15px;
  height: 50px;
  width: 700px;
  /* box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05); */
}

.post-summary:hover {
  background: #e0e0e0;
}

.post-date {
  font-size: 0.8rem;
  color: #666;
  flex-shrink: 0;
  width: 120px;
  font-weight: bold;
}

.post-snippet {
  display: flex;
  font-size: 1rem;
  font-weight: 500;
  margin: 0.5rem 0;
  flex: 1;
  margin-left: 20px;
}

.post-stats,
.user-post-stats {
  font-size: 0.8rem;
  color: #999;
  display: flex;
  justify-content: space-between;
  gap: 15px;
}

.user-post-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.user-post-modal-content {
  background: #fff;
  padding: 2rem;
  border-radius: 10px;
  width: 80%;
  max-width: 600px;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #888;
}

.post-full-content img {
  max-width: 100%;
  margin-top: 1rem;
  border-radius: 5px;
}

.user-post-comments-section {
  align-items: center;
  gap: 1rem;
  border-top: 1px solid #eee;
  padding-top: 10px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.user-post-comment-content {
  flex: 1
}

.user-post-comment {
  border: 1px solid #ddd;
  cursor: pointer;
  transition: box-shadow 0.2s ease;
  display: flex;
  align-items: center;
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 10px;
  gap: 15px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  width: 500px;
}

.post-modal-media-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 10px;
  margin-top: 15px;
}

.post-modal-thumbnail {
  object-fit: cover;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.post-modal-link {
  display: block;
  text-align: center;
  background-color: #007bff;
  color: white;
  padding: 10px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 12px;
}

.post-modal-link:hover {
  background-color: #0056b3;
}

.post-media-preview {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  margin-top: 10px;
}

.media-preview-item {
  width: 60px;
  height: 60px;
  overflow: hidden;
  border-radius: 4px;
  /* background-color: #f0f0f0; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.media-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.media-placeholder {
  font-size: 12px;
  color: #555;
  text-align: center;
}

.media-preview-more {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
  color: #333;
  background-color: #f0f0f0;
  border-radius: 4px;
}

.delete-post-btn {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 8px 8px;
  margin: 8px 0;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
}

.delete-post-btn:hover {
  background-color: #e63939;
}

.trash-icon {
  width: 20px;
  height: 20px;
}