.employee-profile-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    background-color: #f2f2f2;
    min-height: 100vh;
    margin: 0;
}

/* Content area */
.content-flex {
    position: relative;
    top: -90px;
    display: flex;
    justify-content: center;
    gap: 20px;
    width: 100%;
    flex-wrap: wrap;
}

.left-panel {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 540px;
    flex: 0 0 537px;
    /* background-color: white; */
    border-radius: 10px;
    /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */

}

.center-panel {
    position: relative;
    /* top: -27px; */
    flex: 0 0 795px;
    background-color: white;
    border-radius: 10px;
    /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
    text-align: center;
}

.right-panel {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 0 0 537px;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}

.left-panel,
.center-panel,
.right-panel {
    box-sizing: border-box;
}

/* Analytics Section */
.analytics-container {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
    /* padding: 10px 0; */
}

.analytics-stat {
    flex: 1;
    text-align: center;
}

.analytics-stat p {
    font-size: 36px;
    font-weight: bold;
    margin: 0;
    color: #32444D;
}

.analytics-stat span {
    display: block;
    font-size: 16px;
    color: #626E74;
    margin-top: 5px;
}

/* Activity and Events Container */
.activity-events-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    /* margin-top: 20px; */
}

.employee-profile-page .events-gray-box {
    width: 795px;
    height: auto;
    margin-top: 5px;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    overflow-y: auto;
    order: 6;
}

.employee-profile-page .events-list {
    display: flex;
    flex-direction: column;
}

.employee-profile-page .events-gray-box hr {
    border: none;
    border-top: 1px solid #ccc;
    margin: 20px 0;
}

.employee-profile-page .events-gray-box h2 {
    font-weight: bold;
    color: #32444D;
}

.employee-profile-page .events-gray-box h2+.events-list {
    margin-top: 10px;
    padding-top: 20px;
}


/* Responsive Design */
@media (max-width: 768px) {
    .content-flex {
        flex-direction: column;
        /* Stack panels vertically */
        gap: 20px;
        /* Add space between stacked panels */
    }

    .center-panel {
        order: 1;
        /* Show center panel first */
        width: 100%;
        /* Full width */
    }

    .right-panel {
        order: 2;
        /* Show right panel second */
        width: 100%;
        /* Full width */
    }

    .left-panel {
        order: 3;
        /* Show left panel last */
        width: 100%;
        /* Full width */
    }

    .achievements-header,
    .milestones-header {
        flex-direction: column;
        /* Stack filter buttons vertically */
        align-items: flex-start;
        gap: 10px;
    }

    .achievements-filters,
    .milestones-filters {
        justify-content: flex-start;
        gap: 5px;
    }

    .analytics-stat {
        padding: 15px;
        /* Adjust padding for smaller cards */
    }

    .activity,
    .events {
        margin: 10px 0;
    }
}